import { Injectable } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor, HttpResponse } from '@angular/common/http';

import { Observable } from 'rxjs';
import { StorageVars } from './models/storage-vars';

@Injectable()
export class TbAuthInterceptor implements HttpInterceptor {
    constructor() {}

    intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        /**
         * Aggiungo a ogni httprequest l'header 'Authorization' con il nostro token
         */
        let token = JSON.stringify({
            token: localStorage.getItem(StorageVars.JWT)
        });

        if (token) {
            request = request.clone({
                setHeaders: {
                    Authorization: token
                }
            });
        }

        /*
         * Elabora la response di ogni chiamata http
         */
        return next
            .handle(request)
            .pipe
            /**
             * Nel caso di chiamate a tb-loader, ricevo le TbLoaderInfo che vengono messe in cache locale
             */
            // tap((ev: HttpEvent<any>) => {
            //     if (ev instanceof HttpResponse) {
            //         let auth = ev.headers.get("Tb-loader-info");
            //         if (auth) {
            //             let respJson = JSON.parse(auth);
            //             if (respJson) {
            //                 this.infoService.setTbLoaderInfo(new TBLoaderInfo(respJson.tbLoaderName, respJson.tbLoaderId));
            //                 this.logger.debug("TbLoaderInfo -> ", respJson);
            //             }
            //         }
            //     }
            // })
            ();
    }
}
