import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { TbAuthService } from './auth.service';
import { StorageVars } from './models/storage-vars';

@Component({
    selector: 'tb-logout',
    template: ''
})
export class TbLogoffComponent {
    constructor(public authService: TbAuthService, public router: Router) {
        const authtoken = localStorage.getItem(StorageVars.JWT);
        if (authtoken) authService.logoff();
        router.navigate([authService.getLoginUrl()]);
    }
}
